<script context="module">
	let date = new Date(Date.now())
	let storageString = `task-tracker-${date.getMonth()+1}-${date.getDate()}`
	let tasks = (localStorage.getItem(storageString)) ? JSON.parse(localStorage.getItem(storageString)) : [{client: 'eko',task: 'Make a spreadsheet',	startTime: '13:04',	endTime: '13:34'}]
	if (tasks != []) console.log("FETCHED SOME STUFF FROM LOCALSTORAGE!!!")
</script>

<script>
	import { afterUpdate } from 'svelte'
	import TaskTable from './TaskTable.svelte'
	let name = 'world';
	
	function clearTasks() {
		tasks = []
	}
	
	afterUpdate(() => {
		console.log(`I'm gonna store the following string in memory at ${storageString}:\n${JSON.stringify(tasks)}`);
		localStorage.setItem(storageString, JSON.stringify(tasks))
	})
</script>

<header>
	<h1>Task Tracker</h1>
	<h2>{ `${date.getMonth()+1}/${date.getDate()}` }</h2>
	<button on:click={clearTasks}>Clear Tasks</button>
</header>
<main>
	<TaskTable bind:date bind:tasks />
</main>

